*{
	margin: 0px;
	padding: 0px;
	box-sizing: border-box;
	overflow: hidden;
}


.nav-list {
	height: 10vh;
	display: flex;
	width: 50%;
	justify-content: space-around;
	align-items: center;
	margin-left: auto;
	list-style: none;

}

.nav-links {
	text-decoration: none;
	color: black;
	font-size: 25px;
}

.home,
.about {
	height: 100vh;
	position: absolute;
	width: 100%;
}

.home-content,
.about-content {
	height: 100%;
	display: flex;
	justify-content: space-evenly;
	align-items: center;
	img {
		height: 70%;
		
	}
	h1 {
		font-size: 50px;
		margin: 30px 0px;
	}

}

.home {
	background-image: white;
}

.about {
	background-image: linear-gradient(60deg, #abecd6 0%, #fbed96 100%);
}

.button {
  border-radius: 12px;
  color: #4CAF50;
  padding: 15px 32px;
  text-align: center;
  font-size: 16px;
  -webkit-transition-duration: 0.4s; 
  transition-duration: 0.4s;
  box-shadow: 0 6px 12px 0 rgba(0,0,0,0.2), 0 4px 17px 0 rgba(0,0,0,0.19);
}

.button:hover {
  background-color: #4CAF50; /* Green */
  color: white;
}

/* Style the buttons that are used to open and close the accordion panel */
.accordion {
  background-color: #eee;
  color: #444;
  cursor: pointer;
  padding: 18px;
  width: 100%;
  text-align: left;
  border: none;
  outline: none;
  transition: 0.4s;
}

/* Add a background color to the button if it is clicked on (add the .active class with JS), and when you move the mouse over it (hover) */
.active, .accordion:hover {
  background-color: #ccc;
}

/* Style the accordion panel. Note: hidden by default */
.panel {
  padding: 0 18px;
  background-color: white;
  display: none;
  overflow: hidden;
}

.pen {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  height: 100vh;
}
.text {
  color: #4CAF50;
  font-family: 'Roboto', sans-serif;
  padding: 1em 2em;
  text-align: center;
  font-size: 3.5em;
}

#menu-nav {
  max-width: 1000px;
  margin: 130px auto;
  background-color: #ecf0f1;
  border-radius: 4px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
}
 
#navigation-bar ul {
  border-radius: 4px;
  overflow: hidden;
}
#navigation-bar li {
  float: left;
  width: 20%;
}
#navigation-bar li:hover a::before {
  right: 0;
  left: 0;
}
#navigation-bar a {
  color: #4CAF50;
  font-size: 20px;
  text-decoration: none;
  text-align: center;
  padding: 20px 0;
  display: block;
  position: relative;
  transition: all 0.3s ease;
}
#navigation-bar a:hover {
  color: #4CAF50;
}
#navigation-bar a::before {
  content: "";
  transition: all 0.4s ease-in-out;
  position: absolute;
  right: 50%;
  left: 50%;
  bottom: 0;
  height: 2px;
  background-color: #4CAF50;
}
#navigation-bar a:hover::before {
  right: 0;
  left: 0;
}

.box{
  width: 850px;
  background: rgba(0, 0, 0, 0.4);
  padding: 40px;
  text-align: center;
  margin: auto;
  margin-top: 5%;
  color: white;
  font-family: 'Century Gothic',sans-serif;
}
.box-img{
  border-radius: 50%;
  width: 200px;
  height: 200px;

}
.box h1{
  font-size: 40px;
  letter-spacing: 4px;
  font-weight: 100;
}
.box h5{
  font-size: 20px;
  letter-spacing: 3px;
  font-weight: 100;
}
.box p{
  text-align: justify;
}
ul{
  margin: 0;
  padding: 0;
}
.box li{
  display: inline-block;
  margin: 6px;
  list-style: none;
}
.box li a{
  color: white;
  text-decoration: none;
  font-size: 60px;
  transition: all ease-in-out 250ms;
}
.box li a:hover{
  color: #b9b9b9;
}

.loader {
  animation: spin 2s linear infinite;
}

@media screen and (max-width: 670px ) {
	.box {
		width: 100%;
		padding: 20%;
	}

	.box h1{
	  font-size: 200%;
	}

	.box h5{
	  font-size: 75%;
	}



	.box li a{
	  font-size: 350%;
	}

	.about-content {
		width: 100%;
		height: 100%;
		display: flex;
		justify-content: space-evenly;
		align-items: center;
	}

	.home-content {
		h1 {
		font-size: 200%;	
		}
	}
}

/* entire container, keeps perspective */
.flip-container {
  perspective: 1000px;
}
  /* flip the pane when hovered */
  .flip-container:hover .flipper, .flip-container.hover .flipper {
    transform: rotateY(180deg);
  }

.flip-container, .front, .back {
  width: 320px;
  height: 480px;
}

/* flip speed goes here */
.flipper {
  transition: 0.6s;
  transform-style: preserve-3d;

  position: relative;
}

/* hide back of pane during swap */
.front, .back {
  backface-visibility: hidden;

  position: absolute;
  top: 0;
  left: 0;
}

/* front pane, placed above back */
.front {
  z-index: 2;
  /* for firefox 31 */
  transform: rotateY(0deg);
}

/* back, initially hidden pane */
.back {
  transform: rotateY(180deg);
}